.aside {
  min-width: 296px;
  min-height: 100vh;
}

.aside__nav__link {
  transition: 0.3s;
  border-left: 4px solid #f8f9fa;
}

.aside__nav__link:focus {
  color: #198754;
}

.aside__nav__link:hover {
  color: #198754;
  border-left: 4px solid #198754;
}

.aside__nav__link--active {
  color: #198754;
  border-left: 4px solid #198754;
}

.aside__photo {
  width: 40px;
  height: 40px;
}

.editor {
  position: absolute;
  left: -1px;
  bottom: -60px;
  width: calc(100% + 2px);
  visibility: hidden;
}

.editor__btn {
  position: absolute;
  left: 0;
  bottom: 16px;
  width: 100%;
}

.ck-editor__editable_inline {
  min-height: 319px;
}

.custom-form-check {
  display: block;
  min-height: 1.5rem;
  padding-left: 2em;
  margin-bottom: 0.125rem;
}

.custom-form-check .custom-form-check__input {
  float: left;
  margin-left: -2em;
}

.custom-form-check__input {
  width: 1.3em;
  height: 1.3em;
  margin-top: 0.25em;
  vertical-align: top;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: 2px solid rgba(0, 0, 0, 0.75);
  appearance: none;
  color-adjust: exact;
}

.custom-form-check__input[type="checkbox"] {
  border-radius: 0.25em;
}

.custom-form-check__input:checked {
  background-color: #198754;
  border-color: #198754;
}

.custom-form-check__input:focus {
  border-color: #198754;
  outline: none;
  box-shadow: none;
}

.assignment__list {
  min-width: 320px;
}

.assignment__list__item--active {
  background-color: #f8f9fa;
}

.info__photo {
  width: 141px;
  height: 141px;
}

